/** Pro Widget || Start */

i[class*="jkit_pro_"],
.elementor-element:hover i[class*="jkit_pro_"] {
    background-color: unset;
}

i[class*="jkit_pro_"]:before {
    background-size: auto;
    width: 32px;
    height: 32px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    background-color: #556068;
    transition: all 0.1s ease-in-out;
    mask-repeat: no-repeat;
    mask-position: center;
}

.elementor-element {

    &:hover,
    &:focus {
        i[class*="jkit_pro_"]:before {
            background-color: #93003c;
        }
    }
}

.jkit-dark-mode {

    i[class*="jkit_pro_"],
    .elementor-element:hover i[class*="jkit_pro_"] {
        background-color: unset;
    }

    i[class*="jkit_pro_"]:before {
        background-color: #fff;
    }

    .elementor-element {

        &:hover,
        &:focus {
            i[class*="jkit_pro_"]:before {
                background-color: #71d7f7;
            }

            .icon i[class*="jkit_pro_"]:after {
                border-color: #71d7f7;
            }
        }
    }
}

