i[class^="jkit-option-additional"] {
    mask-image: url("../../img/icon.svg");
    mask-repeat: no-repeat;
    mask-size: contain;
    background-color: black;
    filter: invert(88%) sepia(0%) saturate(0%) hue-rotate(163deg) brightness(87%) contrast(0%);
}

i[class^="jkit-pro-option-additional"] {
    background-image: url("../../img/icon-color.svg");
    background-repeat: no-repeat;
    background-size: contain;
}

.elementor-panel-heading-title.elementor-section-title {
    display: flex;
    align-items: center;
    gap: 5px;

    i {
        width: 12px;
        height: 12px;
    }
}

.elementor-panel-menu-item-icon {
    i {
        width: 19px;
        height: 19px;
    }
}

.dialog-buttons-wrapper {
    .jkit-pro-button {
        display: none;
    }
}

.tippy-box.tippy-jkit-pro-promotion {
    background-color: var(--e-a-bg-default);
    box-shadow: var(--e-a-popover-shadow);
    border-radius: var(--e-a-border-radius);

    .tippy-content {
        padding: 0;

        .jkit-pro-banner {
            position: relative;
            color: #515962;

            .header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 26px 22px 22px;
                border-block-end: var(--e-a-border);

                h3 {
                    color: var(--e-a-color-txt-accent);
                    font-size: 14px;
                }

                .close {
                    cursor: pointer;
                    color: var(--e-a-color-txt-disabled);
                }
            }

            .content {
                padding: 22px 22px 26px;
            }
        }
    }

    .tippy-arrow::before {
        color: var(--e-a-bg-default);
    }
}
