#elementor-panel {
	[id^="elementor-panel-category-jkit-pro-"] {
		.elementor-panel-heading-promotion {
			&:not(.jkit-pro-promotion) {
				display: none;
			}

			.jkit-pro-promotion {
				white-space: nowrap;
			}
		}

		.elementor-panel-heading-title {
			text-overflow: ellipsis;
			white-space: nowrap;
			width: auto;
			overflow: hidden;
			direction: rtl;
		}

	}
}
